import React, { useEffect, useState } from 'react';
import Repo from  './repo';


export default () => {
  const [repos, setRepos] = useState([]);
  const [reposCount, setReposCount] = useState([]);


  useEffect(() =>{
    let myRepos;
    const data = sessionStorage.getItem("repos");
    if(data){
      myRepos = JSON.parse(data);

      setReposCount(myRepos.length);

      myRepos = myRepos.slice(0,5);
      return setRepos(myRepos);
    }

    async function fetchRepos(){
      const response = await fetch("https://api.github.com/users/agusfesc/repos");
      myRepos = await response.json();
      setReposCount(myRepos.length);
      sessionStorage.setItem( "repos", JSON.stringify(myRepos) );
      myRepos = myRepos.slice(0,5);
      setRepos(myRepos);
    }

    fetchRepos();
    
  }, []);
  
  return(
    
    <div className="max-w-4xl mx-auto mt-6  pb-16 ">
        <header className="text-center text-gray-800">
          <h2 className="text-3xl font-medium py-5"> Mis desarrollos :)</h2>
        </header>
        <ul className="repos-list">
          {
              repos.map( (repo) =>{
                  return <Repo repo={repo} key={repo.id}/>
              })
          }
        </ul>
        <div className=" mt-12 text-center ">
        <a href="https://github.com/agusfesc" className="btn" target="_blank" rel="noopener noreferrer" >
          Total de proyectos subidos: ({reposCount})
        </a>
        
        </div>
    </div>
  );
};