import React from 'react';

export default (props)=> (
    <li className="flex flex-wrap overflow-hidden">
        <div className="">           
            <a href={props.repo.html_url} target="_blank" className="text-blue-600 font-semibold no-underline  text-blue-500"  rel="noopener noreferrer"> {props.repo.name}</a>
            <p className="text-gray-800 overflow-y-hidden py-1 text-lg font-hairline " style={{height:"1.8em"}}> {props.repo.description}</p>
            <p className=" text-gray-800 overflow-y-hidden py-1 text-lg font-light ">Desarrollado en: {props.repo.language}</p>
            
        </div>
        
    </li>
);