import React from 'react';
import illustration from '../images/dev_productivity.svg'

export default () => (
    <header className="bg-blue-200 flex items-center justify-between flex-wrap p-6  border-b-2 border-indigo-200 rounded-sm ">
        
        <div className="container mx-auto p-12 max-w-4xl ">
            <div className="flex justify-center items-center"> 
                <div className="flex-1">
                    <h1 className="font-medium text-4xl text-gray-800"> Hola, soy Agustín!</h1>
                    <p className="text-xl font-light"> Desarrollador Junior</p>
                    <p className="text-xl font-light"> Por ahora orientado a la creacion de Apps Mobile</p>
                    <p className="text-xl font-light"> ¿Quien sabe en un futuro?</p>
                </div>
                    <img src={illustration} alt="Dev Productivity" style={ { height:"330px" } }></img>
            </div>
            
            
           
        
        </div>
        
    
    </header>


)